import React, { Fragment, useEffect, useState } from "react";
import axios from "axios";
import { CoinList } from "../Config/Api";
import { CryptoState } from "../Config/CryptoContext";
import Search from "./Search";
import Pagination from "./Pagination";
const Table = () => {
  const { currency, numberWithCommas, symbol } = CryptoState();
  const [coinList, setCoinList] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [postPerPage, setPostPerPage] = useState(10);
  const coins = async () => {
    const { data } = await axios.get(CoinList(currency));
    setCoinList(data);
  };
  useEffect(() => {
    coins();
  }, [currency]);
  //   console.log(coinList);
  const lastPostIndex = currentPage * postPerPage;
  const firstPostIndex = lastPostIndex - postPerPage;
  const coinsData = coinList.slice(firstPostIndex, lastPostIndex);

  const tableHead = (
    <div className="flex   md:mx-10 justify-between my-5  md:my-10 font-bold border-b text-lg md:xl lg:xxl">
      <div className="w-full text-center">Coin</div>
      <div className="w-full text-center">Price</div>
      <div className="w-full text-center">24h Change</div>
      <div className="w-full text-center">Market Cap</div>
    </div>
  );

  const tableBody = coinsData.map((coin) => {
    let profit = coin.price_change_percentage_24h >= 0;
    return (
      <div className="flex  md:mx-10  my-5  md:py-5 lg:py-5  border-b   hover:bg-gray-100 items-center">
        <div className="w-full md:text-center lg:text-center text-center flex flex-col items-center  ">
          <img className="w-5 mb-1" src={coin.image} alt="" />

          <h2>{coin.name}</h2>
        </div>
        <div className="w-full text-center">
          {symbol} {numberWithCommas(coin.current_price.toFixed(2))}
        </div>
        <div
          className={
            (profit && "text-green-400 w-full text-center") ||
            "text-red-400 w-full text-center "
          }
        >
          ({profit && "+"}
          {coin?.price_change_percentage_24h.toFixed(2)}%)
        </div>
        <div className="text-center w-full">
          {numberWithCommas(coin.market_cap.toString().slice(0, -6))}M
        </div>
      </div>
    );
  });

  return (
    <Fragment>
      <Search />
      {tableHead}
      {tableBody}
      <Pagination
        postPerPage={postPerPage}
        totalPost={coinList.length}
        firstPostIndex={firstPostIndex}
        lastPostIndex={lastPostIndex}
        setCurrentPage={setCurrentPage}
        currentPage={currentPage}
      />
    </Fragment>
  );
};

export default Table;
